import React from "react";
import "./HomeComponent1.css";
import Button from "react-bootstrap/Button";

function HomeComponent1() {
  return (
    <div className="homeComponent1">
      <section className="component1Title">
        <p>
          ENCONTRE A MORADIA
          <br />
          PERFEITA PARA VOCÊ
        </p>
      </section>

      <section className="component1Subtitle">
        <p>O melhor site para alugar quartos ou montar sua república.</p>
        <div className="component1Button">
          <Button
            variant="light"
            size="lg"
            style={{
              paddingRight: "15%",
              paddingLeft: "15%",
              marginBottom: "5%",
            }}
            href="/buscarMoradia"
          >
            Buscar
          </Button>
        </div>
      </section>
    </div>
  );
}

export default HomeComponent1;
