import React, { useState, useEffect, useContext } from "react";
import { AuthContext } from "../../contexts/auth";
import Axios from "axios";

import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Form from "react-bootstrap/Form";

import "./UpdateVaga.css";

function UpdateVaga() {
  const { user } = useContext(AuthContext);
  function atualizarVaga() {
    if (
      nomeVaga === "" ||
      descricao === "" ||
      tipoVaga === "" ||
      valorAluguel === "" ||
      velInternet === ""
    ) {
      alert("Informações faltando. Verifique e tente novamente");
    } else if (velInternet < 1) {
      alert("Digite uma velocidade de internet válida");
    } else if (valorAluguel < 1) {
      alert("Digite um valor de aluguel válido");
    } else if (fotoPerfil === null || fotoFile.type.startsWith("image/")) {
      atualizarVagaInfo();
    } else {
      alert("Tipo de arquivo de foto inválido");
    }
  }

  const atualizarVagaInfo = () => {
    if (fotoPerfil !== null) {
      Axios.post("http://localhost:3001/api/updateVagaComFoto", {
        nomeVaga: nomeVaga,
        descricao: descricao,
        tipoVaga: tipoVaga,
        valorAluguel: valorAluguel,
        velInternet: velInternet,
        quartoIndividual: quartoIndividual,
        quartoMobiliado: quartoMobiliado,
        contasInclusas: contasInclusas,
        vagaEstacionamento: vagaEstacionamento,
        banheiroIndividual: banheiroIndividual,
        fotoPerfil: fotoPerfil,
        vagaId: vagaId,
      })
        .then((response) => {
          console.log(response);
          window.location.assign("/minhasVagas");
        })
        .catch((err) => {
          if (err.response) {
            alert("ERRO: Foto muito pesada. Escolha uma de até 2MB");
            console.log(err.response.data);
            console.log(err.response.status);
            console.log(err.response.headers);
          }
        });
    }
    // else {
    //   Axios.post("http://localhost:3001/api/updateVaga", {
    //     nomeVaga: nomeVaga,
    //     descricao: descricao,
    //     tipoVaga: tipoVaga,
    //     valorAluguel: valorAluguel,
    //     velInternet: velInternet,
    //     quartoIndividual: quartoIndividual,
    //     quartoMobiliado: quartoMobiliado,
    //     contasInclusas: contasInclusas,
    //     vagaEstacionamento: vagaEstacionamento,
    //     banheiroIndividual: banheiroIndividual,
    //     vagaId: vagaId,
    //   })
    //     .then((response) => {
    //       window.location.assign("/minhasVagas");
    //     })
    //     .catch((err) => {
    //       if (err.response) {
    //         console.log(err.response.data);
    //         console.log(err.response.status);
    //         console.log(err.response.headers);
    //       }
    //     });
    // }
  };
  const [vacanciesList, setVacanciesList] = useState([]);

  let vagaId = parseInt(window.location.href.toString().slice(41));
  const authUserId = user.id;

  useEffect(() => {
    Axios.post("http://localhost:3001/api/getVaga", { vagaId }).then(
      (response) => {
        setVacanciesList(response.data);
        if (response.data.length < 1) {
          window.location.href = "http://localhost:3000/404";
        }

        setNomeVaga(response.data[0].nomeVaga);
        setDescricao(response.data[0].descricao);
        setTipoVaga(response.data[0].tipoVaga);
        setValorAluguel(response.data[0].valorAluguel);
        setQuartoIndividual(response.data[0].quartoIndividual);
        setQuartoMobiliado(response.data[0].quartoMobiliado);
        setContasInclusas(response.data[0].contasInclusas);
        setVagaEstacionamento(response.data[0].vagaEstacionamento);
        setBanheiroIndividual(response.data[0].banheiroIndividual);
        setVelInternet(response.data[0].velInternet);

        setUserId(response.data[0].Usuarios_idUsuarios);
      }
    );
  }, []);

  const [nomeVaga, setNomeVaga] = useState("");
  const [descricao, setDescricao] = useState("");
  const [tipoVaga, setTipoVaga] = useState("");
  const [valorAluguel, setValorAluguel] = useState("");
  const [quartoIndividual, setQuartoIndividual] = useState(false);
  const [quartoMobiliado, setQuartoMobiliado] = useState(false);
  const [contasInclusas, setContasInclusas] = useState(false);
  const [vagaEstacionamento, setVagaEstacionamento] = useState(false);
  const [banheiroIndividual, setBanheiroIndividual] = useState(false);
  const [velInternet, setVelInternet] = useState("");
  const [fotoPerfil, setFotoPerfil] = useState(null);
  const [fotoFile, setFotoFile] = useState(null);

  const [userId, setUserId] = useState(null);

  return (
    <div style={{ paddingBottom: "5%" }}>
      {userId === authUserId && (
        <div>
          <h1
            style={{
              paddingTop: "5%",
              textAlign: "center",
              color: "white",
            }}
          >
            Atualizar Vaga
          </h1>
          {vacanciesList.map((val) => {
            return (
              <div
                style={{
                  marginTop: "5%",
                  width: "60%",
                  margin: "3% auto",
                  backgroundColor: "white",
                  borderRadius: "20px",
                  paddingBottom: "3%",
                }}
              >
                <div className="form" id="form">
                  <div className="pt-5">
                    <label>Nome da Vaga</label> &nbsp;
                    <small>maximo de 30 caracteres</small> <br />
                    <input
                      type="text"
                      name="nome"
                      className="input"
                      maxLength="30"
                      value={val.nomeVaga}
                      onChange={(e) => {
                        setNomeVaga(e.target.value);
                        val.nomeVaga = e.target.value;
                      }}
                    />
                  </div>
                  <div className="pt-4">
                    <label>Tipo da Vaga</label> <br />
                    <select
                      value={val.tipoVaga}
                      onChange={(e) => {
                        setTipoVaga(e.target.value);
                        val.tipoVaga = e.target.value;
                      }}
                    >
                      <option value="Apartamento - Quarto Individual - Feminino">
                        Apartamento - Quarto Individual - Feminino
                      </option>
                      <option value="Apartamento - Quarto Individual - Masculino">
                        Apartamento - Quarto Individual - Masculino
                      </option>
                      <option value="Apartamento - Quarto Individual - Misto">
                        Apartamento - Quarto Individual - Misto
                      </option>
                      <option value="Apartamento - Quarto Coletivo - Feminino">
                        Apartamento - Quarto Coletivo - Feminino
                      </option>
                      <option value="Apartamento - Quarto Coletivo - Masculino">
                        Apartamento - Quarto Coletivo - Masculino
                      </option>
                      <option value="Apartamento - Quarto Coletivo - Misto">
                        Apartamento - Quarto Coletivo - Misto
                      </option>
                      <option value="Casa - Quarto Individual - Feminino">
                        Casa - Quarto Individual - Feminino
                      </option>
                      <option value="Casa - Quarto Individual - Masculino">
                        Casa - Quarto Individual - Masculino
                      </option>
                      <option value="Casa - Quarto Individual - Misto">
                        Casa - Quarto Individual - Misto
                      </option>
                      <option value="Casa - Quarto Coletivo - Feminino">
                        Casa - Quarto Coletivo - Feminino
                      </option>
                      <option value="Casa - Quarto Coletivo - Masculino">
                        Casa - Quarto Coletivo - Masculino
                      </option>
                      <option value="Casa - Quarto Coletivo - Misto">
                        Casa - Quarto Coletivo - Misto
                      </option>
                      <option value="Kitnet">Kitnet</option>
                      <option value="Outros">Outros</option>
                    </select>
                  </div>
                  <div className="pt-4">
                    <label>Descrição da Vaga</label> &nbsp;
                    <small>máximo de 150 caracteres</small> <br />
                    <input
                      placeholder="Escreva aqui informações mais detalhadas da vaga e das pessoas que moram lá (caso haja)"
                      maxLength="150"
                      value={val.descricao}
                      className="input-description"
                      onChange={(e) => {
                        setDescricao(e.target.value);
                        val.descricao = e.target.value;
                      }}
                    />
                  </div>
                  <Row className="pt-4">
                    <label>
                      Valor Mensal do Aluguel <small>em R$</small>
                    </label>
                    <br />
                    <Col md>
                      <div>
                        <input
                          placeholder="500.00"
                          type="number"
                          min={1}
                          value={val.valorAluguel}
                          className="input"
                          onChange={(e) => {
                            setValorAluguel(e.target.value);
                            val.valorAluguel = e.target.value;
                          }}
                        />
                      </div>
                    </Col>
                    <Col md>
                      <div>
                        <Form>
                          <Form.Check
                            checked={val.contasInclusas}
                            type="switch"
                            id="custom-switch"
                            label="As contas já vêm inclusas nesse valor?"
                            onChange={(e) => {
                              setContasInclusas(e.target.checked);
                              val.contasInclusas = e.target.checked;
                            }}
                          />
                        </Form>
                      </div>
                    </Col>
                  </Row>
                  <Row className="pt-4">
                    <Col md>
                      <Form>
                        <Form.Check
                          checked={val.quartoIndividual}
                          type="switch"
                          id="custom-switch"
                          label="Quarto é individual?"
                          onChange={(e) => {
                            setQuartoIndividual(e.target.checked);
                            val.quartoIndividual = e.target.checked;
                          }}
                        />
                      </Form>
                      <Form>
                        <Form.Check
                          checked={val.banheiroIndividual}
                          type="switch"
                          id="custom-switch"
                          label="Banheiro é individual?"
                          onChange={(e) => {
                            setBanheiroIndividual(e.target.checked);
                            val.banheiroIndividual = e.target.checked;
                          }}
                        />
                      </Form>
                    </Col>
                    <Col md>
                      <Form>
                        <Form.Check
                          checked={val.quartoMobiliado}
                          type="switch"
                          id="custom-switch"
                          label="Quarto é mobiliado?"
                          onChange={(e) => {
                            setQuartoMobiliado(e.target.checked);
                            val.quartoMobiliado = e.target.checked;
                          }}
                        />
                      </Form>
                      <Form>
                        <Form.Check
                          checked={val.vagaEstacionamento}
                          type="switch"
                          id="custom-switch"
                          label="Possui vaga de estacionamento?"
                          onChange={(e) => {
                            setVagaEstacionamento(e.target.checked);
                            val.vagaEstacionamento = e.target.checked;
                          }}
                        />
                      </Form>
                    </Col>
                  </Row>
                  <Row className="pt-4">
                    <Col md>
                      <label>Velocidade da Internet</label> &nbsp;
                      <small>em Mbps</small>
                      <br />
                      <input
                        placeholder="500.00"
                        type="number"
                        min={1}
                        value={val.velInternet}
                        className="input"
                        onChange={(e) => {
                          setVelInternet(e.target.value);
                          val.velInternet = e.target.value;
                        }}
                      />
                    </Col>
                    <Col md></Col>
                  </Row>
                  <div className="pt-4">
                    <Form.Group className="mb-3">
                      <Form.Label style={{ color: "black" }}>
                        Foto Principal
                      </Form.Label>
                      <Form.Control
                        type="file"
                        id="fotoInput"
                        name="fotoInput"
                        accept="image/png, image/jpeg"
                        onChange={(e) => {
                          const file = e.target.files[0];
                          const reader = new FileReader();
                          reader.addEventListener("load", () => {
                            setFotoPerfil(reader.result);
                          });
                          if (file !== undefined) {
                            reader.readAsDataURL(file);
                            setFotoFile(file);
                          }
                        }}
                      />
                    </Form.Group>
                    <br />
                    <Button
                      variant="success"
                      className="mt-4"
                      onClick={atualizarVaga}
                      size="lg"
                    >
                      Atualizar
                    </Button>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      )}
      {userId !== authUserId && (
        <>
          <section style={{ marginTop: "10%" }}>
            <h1 style={{ textAlign: "center", color: "white" }}>ERRO: 401</h1>

            <br />
            <h1 style={{ textAlign: "center", color: "white" }}>
              Suas credenciais não permitem acesso à essa página
            </h1>
          </section>
        </>
      )}
    </div>
  );
}

export default UpdateVaga;
