import React, { useState, useEffect } from "react";
import Axios from "axios";

import "./VacancyCards.css";
// import { FilterVacancies } from "../components/common";
import { Card } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import { FaMapMarkerAlt } from "react-icons/fa";
import FilterVacancies from "../../filterVacancies/filterVacancies";
import FilterVacanciesType from "../../filterVacanciesType/filterVacanciesType";

function VacancyCards() {
  const [vacanciesList, setVacanciesList] = useState([]);
  const [filterValue, setFilterValue] = useState();
  const [filterType, setFilterType] = useState();

  let filteredVacanciesList = vacanciesList.filter((vacancie) => {
    if (
      filterValue === "" ||
      filterValue === null ||
      filterValue === undefined
    ) {
      return vacancie;
    } else {
      return vacancie.valorAluguel <= filterValue;
    }
  });

  let filteredVacanciesType = filteredVacanciesList.filter((vacancie) => {
    if (
      filterType === "" ||
      filterType === null ||
      filterType === undefined ||
      filterType === "Todos"
    ) {
      return vacancie;
    } else if (filterType === "Apartamento - Quarto Individual - Feminino") {
      return vacancie.tipoVaga === "Apartamento - Quarto Individual - Feminino";
    } else if (filterType === "Apartamento - Quarto Individual - Masculino") {
      return (
        vacancie.tipoVaga === "Apartamento - Quarto Individual - Masculino"
      );
    } else if (filterType === "Apartamento - Quarto Individual - Misto") {
      return vacancie.tipoVaga === "Apartamento - Quarto Individual - Misto";
    } else if (filterType === "Apartamento - Quarto Coletivo - Feminino") {
      return vacancie.tipoVaga === "Apartamento - Quarto Coletivo - Feminino";
    } else if (filterType === "Apartamento - Quarto Coletivo - Masculino") {
      return vacancie.tipoVaga === "Apartamento - Quarto Coletivo - Masculino";
    } else if (filterType === "Apartamento - Quarto Coletivo - Misto") {
      return vacancie.tipoVaga === "Apartamento - Quarto Coletivo - Misto";
    } else if (filterType === "Casa - Quarto Individual - Feminino") {
      return vacancie.tipoVaga === "Casa - Quarto Individual - Feminino";
    } else if (filterType === "Casa - Quarto Individual - Masculino") {
      return vacancie.tipoVaga === "Casa - Quarto Individual - Masculino";
    } else if (filterType === "Casa - Quarto Individual - Misto") {
      return vacancie.tipoVaga === "Casa - Quarto Individual - Misto";
    } else if (filterType === "Casa - Quarto Coletivo - Feminino") {
      return vacancie.tipoVaga === "Casa - Quarto Coletivo - Feminino";
    } else if (filterType === "Casa - Quarto Coletivo - Masculino") {
      return vacancie.tipoVaga === "Casa - Quarto Coletivo - Masculino";
    } else if (filterType === "Casa - Quarto Coletivo - Misto") {
      return vacancie.tipoVaga === "Casa - Quarto Coletivo - Misto";
    } else if (filterType === "Kitnet") {
      return vacancie.tipoVaga === "Kitnet";
    } else if (filterType === "Outros") {
      return vacancie.tipoVaga === "Outros";
    }
    return vacancie.tipoVaga === "";
  });

  useEffect(() => {
    Axios.get("http://localhost:3001/api/getVacancies").then((response) => {
      setVacanciesList(response.data);
    });
  }, []);

  function onFilterValueSelected(filterValue) {
    setFilterValue(filterValue);
  }
  function onFilterTypeSelected(filterType) {
    setFilterType(filterType);
  }

  return (
    <>
      <div>
        <h1
          style={{
            paddingTop: "2%",
            paddingBottom: "1%",
            textAlign: "center",
            color: "white",
          }}
        >
          Encontrar Moradia
        </h1>
      </div>
      <div style={{ display: "flex", marginLeft: "2%" }}>
        <FilterVacancies
          filterValueSelected={onFilterValueSelected}
        ></FilterVacancies>
        <FilterVacanciesType
          filterTypeSelected={onFilterTypeSelected}
        ></FilterVacanciesType>
      </div>

      <section className="cards">
        {filteredVacanciesType.map((val) => {
          return (
            <Card style={{ width: "16rem", height: "27rem" }} className="card">
              <Card.Img
                variant="top"
                src={val.imgPrincipal}
                className="card-img"
              ></Card.Img>
              <Card.Body className="text-center">
                <Card.Title style={{ fontSize: "16px" }}>
                  {val.nomeVaga}
                </Card.Title>
                <Card.Title style={{ fontSize: "14px" }}>
                  <FaMapMarkerAlt /> {val.bairro}
                </Card.Title>
                <Card.Text style={{ fontSize: "14px" }}>
                  R${val.valorAluguel}{" "}
                  <b>{val.contasInclusas ? "" : " + contas"}</b>
                </Card.Text>
                <Card.Text style={{ fontSize: "12px" }}>
                  {val.descricao}
                </Card.Text>
                <Button
                  variant="success"
                  size="sm"
                  href={"/moradia/" + val.idVaga}
                  className="card-btn"
                >
                  Mais Informações
                </Button>{" "}
              </Card.Body>
            </Card>
          );
        })}
      </section>
    </>
  );
}

export default VacancyCards;
