import React from "react";
import "./About.css";

function About() {
  return (
    <>
      <div style={{ paddingBottom: "5%", backgroundColor: "#004d00" }}>
        <h1
          style={{
            paddingTop: "2%",
            textAlign: "center",
            color: "white",
          }}
        >
          Sobre
        </h1>
        <div
          style={{
            marginTop: "5%",
            width: "60%",
            margin: "3% auto",
            backgroundColor: "white",
            borderRadius: "20px",
            paddingBottom: "3%",
          }}
        >
          <div className="form" id="form">
            <div className="pt-5">
              {/* <h4 style={{ color: "green" }}>Motivação</h4> */}
              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                O ingresso no ensino superior requer que estudantes procurem
                moradia, principalmente se ainda não residem na mesma cidade
                onde estudarão. Além disso, compartilhar uma moradia ajuda a
                reduzir os custos gerais e proporciona uma maior convivência com
                colegas do campus. Muitas vezes essa busca por um lugar para
                morar precisa ser feita em um curto período, entre a convocação
                para matrícula e o início das aulas, mas também pode ocorrer ao
                longo de toda graduação dependendo da realidade de cada um. E
                nós sabemos como essa busca pode ser difícil.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Diante desse contexto e visando contribuir com uma solução para
                estudantes, Palloma Vilela Tiba e Victor Greghi Hernandez,
                alunos da 1ª turma de Engenharia da Computação do IFSP campus
                Piracicaba, com a orientação do Prof. Dr. Thiago Luís Lopes
                Siqueira, propuseram o desenvolvimento de um software que
                objetiva viabilizar o anúncio e a procura de vagas em moradias,
                bem como a formação de grupos para dividirem uma moradia.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Cada integrante contribuiu com diferentes habilidades e
                expertise que juntas resultaram nessa plataforma. O Victor
                Greghi agregou com a modelagem de dados, com conhecimentos nas
                tecnologias front-end e back-end utilizadas no desenvolvimento e
                com operações no banco de dados. Além disso, sua influência como
                presidente da Atlética do campus contribuiu para recrutar alunos
                na fase de teste e continuará contribuindo na etapa de
                divulgação da plataforma para os estudantes.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Já a integrante Palloma Tiba auxiliou no desenho do projeto, sua
                visão de design contribuiu para criação da parte visual do site,
                criação do nosso nome e logo. Além disso, ela também contribuiu
                no desenvolvimento front-end, back-end e banco de dados.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Esse projeto associa-se com dois Objetos de Desenvolvimento
                Sustentável (ODS). O objetivo 11.1, no qual refere-se a garantir
                o acesso de todos à habitação segura, adequada e a preço
                acessível e com o objeto 4 como um todo, no que se refere ao
                acesso à educação de qualidade.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                Apesar de existirem outras plataformas disponíveis para busca de
                moradia, o IFHome se diferencia por ser pensado e criado
                especialmente para você, estudante. Nossa missão é facilitar,
                cada dia mais, sua inserção no ambiente acadêmico. Para isso,
                buscamos evoluir nossa plataforma diariamente para atender as
                suas necessidades.
              </p>

              <p style={{ textIndent: "1.5em", textAlign: "justify" }}>
                <strong>Obrigado por utilizar o IFHome!</strong>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default About;
