import React, { useState, useEffect, useContext } from "react";
import { AuthContext } from "../../contexts/auth";
import Axios from "axios";

import InputMask from "react-input-mask";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";

import "./UpdateUsersInfo.css";

function UpdateUsersInfo() {
  const { user, logout } = useContext(AuthContext);
  console.log(user);
  function cadastrarUsuario() {
    if (nome === "" || email === "" || telefone === "") {
      alert("Informações faltando. Verifique e tente novamente");
    } else if (validarNome(nome) === false) {
      alert("Digite um nome válido");
    } else if (validarEmail(email) === false) {
      alert("Digite um e-mail válido");
    } else if (
      telefone === "(00) 00000-0000" ||
      telefone === "(11) 11111-1111" ||
      telefone === "(22) 22222-2222" ||
      telefone === "(33) 33333-3333" ||
      telefone === "(44) 44444-4444" ||
      telefone === "(55) 55555-5555" ||
      telefone === "(66) 66666-6666" ||
      telefone === "(77) 77777-7777" ||
      telefone === "(88) 88888-8888" ||
      telefone === "(99) 99999-9999" ||
      telefone.endsWith("_")
    ) {
      alert("Digite um telefone válido");
    } else if (fotoPerfil === null || fotoFile.type.startsWith("image/")) {
      updateUsuario();
    } else {
      alert("Tipo de arquivo de foto inválido");
    }
  }

  function validarEmail(email) {
    var re = /\S+@\S+\.\S+/;
    return re.test(email);
  }

  function validarNome(nome) {
    var re = /^[A-Za-záàâãéèêíïóôõöúçñÁÀÂÃÉÈÍÏÓÔÕÖÚÇÑ ]+$/;
    return re.test(nome);
  }

  function formatarData(data) {
    let [year, month, day] = data.split("-");
    day = day.slice(0, 2);
    return year + "-" + month + "-" + day;
  }

  const updateUsuario = () => {
    if (fotoPerfil !== null) {
      Axios.post("http://localhost:3001/api/updateUserComFoto", {
        nome: nome,
        email: email,
        telefone: telefone,
        procuraVaga: procuraVaga,
        fotoPerfil: fotoPerfil,
        id: user.id,
        curso: curso,
        descricao: descricao,
      })
        .then((response) => {
          window.location.assign("/home");
        })
        .catch((err) => {
          if (err.response) {
            alert("ERRO: Foto de perfil muito pesada. Escolha uma de até 2MB");
            console.log(err.response.data);
            console.log(err.response.status);
            console.log(err.response.headers);
          }
        });
    } else {
      Axios.post("http://localhost:3001/api/updateUser", {
        nome: nome,
        email: email,
        telefone: telefone,
        procuraVaga: procuraVaga,
        id: user.id,
        curso: curso,
        descricao: descricao,
      })
        .then((response) => {
          window.location.assign("/home");
        })
        .catch((err) => {
          if (err.response) {
            console.log(err.response.data);
            console.log(err.response.status);
            console.log(err.response.headers);
          }
        });
    }
  };

  const deleteUser = () => {
    console.log(user.id);
    Axios.post("http://localhost:3001/api/deleteUser", {
      userId,
    })
      .then((response) => {
        handleLogout();
      })
      .catch((err) => {
        if (err.response) {
          console.log(err.response.data);
          console.log(err.response.status);
          console.log(err.response.headers);
        }
      });
  };

  const handleLogout = () => {
    logout();
  };

  const [usersList, setUsersList] = useState([]);
  const userId = user.id;

  useEffect(() => {
    Axios.post("http://localhost:3001/api/getUser", { userId }).then(
      (response) => {
        setUsersList(response.data);
        setNome(response.data[0].nome);
        setEmail(response.data[0].email);
        setTelefone(response.data[0].telContato);
        setprocuraVaga(response.data[0].procuraVaga);
        setCurso(response.data[0].curso);
        setDescricao(response.data[0].descricao);
      }
    );
  }, []);

  const [nome, setNome] = useState("");
  const [email, setEmail] = useState("");
  const [telefone, setTelefone] = useState("");
  const [procuraVaga, setprocuraVaga] = useState("");
  const [fotoPerfil, setFotoPerfil] = useState(null);
  const [curso, setCurso] = useState("");
  const [fotoFile, setFotoFile] = useState(null);
  const [descricao, setDescricao] = useState("");

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <div style={{ paddingBottom: "5%", backgroundColor: "#004d00" }}>
        <h1
          style={{
            paddingTop: "5%",
            textAlign: "center",
            color: "white",
          }}
        >
          Atualizar Meu Cadastro
        </h1>
        {usersList.map((val) => {
          return (
            <div
              style={{
                marginTop: "5%",
                width: "60%",
                margin: "3% auto",
                backgroundColor: "white",
                borderRadius: "20px",
                paddingBottom: "3%",
              }}
            >
              <div className="form" id="form">
                <div className="pt-5">
                  <label>Nome</label> <br />
                  <input
                    type="text"
                    name="nome"
                    className="input"
                    maxLength="45"
                    value={val.nome}
                    onChange={(e) => {
                      setNome(e.target.value);
                      val.nome = e.target.value;
                    }}
                  />
                </div>
                <Row>
                  <Col md>
                    <div className="pt-4">
                      <label>Data de nascimento</label> <br />
                      <input
                        type="date"
                        name="dataNasc"
                        className="input"
                        disabled
                        value={formatarData(val.dataNascimento)}
                      />
                    </div>
                  </Col>
                  <Col md>
                    <div className="pt-4">
                      <label>CPF</label> <br />
                      <InputMask
                        mask="999.999.999-99"
                        name="cpf"
                        disabled
                        value={val.cpf}
                        className="input"
                      />
                    </div>
                  </Col>
                  <Col md>
                    <div className="pt-4">
                      <label>Telefone</label> <br />
                      <InputMask
                        mask="(99) 99999-9999"
                        name="celContato"
                        className="input"
                        value={val.telContato}
                        onChange={(e) => {
                          setTelefone(e.target.value);
                          val.telContato = e.target.value;
                        }}
                      />
                    </div>
                  </Col>
                </Row>
                <Row>
                  <div className="pt-4">
                    <label>E-mail</label> <br />
                    <input
                      type="email"
                      name="email"
                      maxLength="45"
                      className="input"
                      value={email}
                      onChange={(e) => {
                        setEmail(e.target.value);
                        val.email = e.target.value;
                      }}
                    />
                  </div>
                </Row>
                {val.aluno === 1 && (
                  <Row>
                    <div className="pt-4">
                      <label>Curso</label> <br />
                      <select
                        value={val.curso}
                        onChange={(e) => {
                          setCurso(e.target.value);
                          val.curso = e.target.value;
                        }}
                      >
                        <option value="Engenharia da Computação">
                          Engenharia da Computação
                        </option>
                        <option value="Engenharia Mecânica">
                          Engenharia Mecânica
                        </option>
                        <option value="Engenharia Elétrica">
                          Engenharia Elétrica
                        </option>
                        <option value="Física">Física</option>
                        <option value="Automação Industrial">
                          Automação Industrial
                        </option>
                      </select>
                    </div>
                  </Row>
                )}
                <div className="pt-4">
                  <Form.Group className="mb-3">
                    <Form.Label style={{ color: "black" }}>
                      Foto de Perfil
                    </Form.Label>
                    <Form.Control
                      type="file"
                      id="fotoInput"
                      name="fotoInput"
                      max-size="50"
                      accept="image/png, image/jpeg"
                      onChange={(e) => {
                        const file = e.target.files[0];
                        const reader = new FileReader();
                        reader.addEventListener("load", () => {
                          setFotoPerfil(reader.result);
                        });
                        if (file !== undefined) {
                          reader.readAsDataURL(file);
                          setFotoFile(file);
                        }
                      }}
                    />
                  </Form.Group>
                </div>
                {val.aluno === 1 && (
                  <div className="pt-4 align">
                    <Form>
                      <Form.Check
                        checked={val.procuraVaga}
                        type="switch"
                        id="custom-switch"
                        label="Deixar meu perfil a mostra para usuários que desejam
                        montar repúblicas"
                        onChange={(e) => {
                          setprocuraVaga(e.target.checked);
                          val.procuraVaga = e.target.checked;
                        }}
                      />
                    </Form>
                    {val.procuraVaga === true && (
                      <div className="pt-4">
                        <label>Sobre Você</label> &nbsp;
                        <small>máximo de 300 caracteres</small> <br />
                        <input
                          placeholder="Escreva aqui informações mais detalhadas da vaga e das pessoas que moram lá (caso haja)"
                          value={val.descricao}
                          maxLength="300"
                          className="input-description"
                          onChange={(e) => {
                            setDescricao(e.target.value);
                            val.descricao = e.target.value;
                          }}
                        />
                      </div>
                    )}
                    {val.procuraVaga === 1 && (
                      <div className="pt-4">
                        <label>Sobre Você</label> &nbsp;
                        <small>máximo de 300 caracteres</small> <br />
                        <input
                          placeholder="Escreva aqui um pouco sobre você, seus hobbies, interesses e o que você busca em uma república."
                          value={val.descricao}
                          maxLength="300"
                          className="input-description"
                          onChange={(e) => {
                            setDescricao(e.target.value);
                            val.descricao = e.target.value;
                          }}
                        />
                      </div>
                    )}
                  </div>
                )}
                <br />
                <Button
                  variant="success"
                  className="mt-4"
                  onClick={cadastrarUsuario}
                  size="lg"
                >
                  Atualizar
                </Button>
                &nbsp;
                <Button
                  variant="outline-danger"
                  className="mt-4"
                  onClick={handleShow}
                  size="lg"
                >
                  Deletar meu perfil
                </Button>
              </div>
            </div>
          );
        })}
        <Modal show={show} onHide={handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>Opa! Tem certeza?</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            Essa alteração será permanente. Ao confirmar abaixo você não poderá
            retornar a menos que crie uma nova conta no IFHOME.
          </Modal.Body>
          <Modal.Footer>
            <Button variant="success" onClick={handleClose}>
              Cancelar
            </Button>
            <Button variant="danger" onClick={deleteUser}>
              Deletar Conta
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    </>
  );
}

export default UpdateUsersInfo;
