import React, { useState, useEffect } from "react";
import Axios from "axios";

import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";

import "./UserExpanded.css";

function UserExpanded() {
  const [userList, setUserList] = useState([]);
  let userId = parseInt(window.location.href.toString().slice(27));

  useEffect(() => {
    Axios.post("http://localhost:3001/api/getUser", { userId }).then(
      (response) => {
        setUserList(response.data);
        if (response.data.length < 1) {
          window.location.href = "http://localhost:3000/404";
        }
      }
    );
  }, []);

  function getIdade(date) {
    date = Date.parse(date);
    var diferenca = Date.now() - date;
    var idade = new Date(diferenca);
    return Math.abs(idade.getUTCFullYear() - 1970);
  }

  return (
    <div>
      {userList.map((val) => {
        return (
          <div
            className="userContainer"
            style={{
              width: "90%",
              margin: "4% auto",
              backgroundColor: "white",
              borderRadius: "20px",
              paddingBottom: "5%",
            }}
          >
            <div>
              <Button
                href="/montarRepublica"
                variant="success"
                className="user-btn"
                style={{
                  marginLeft: "5%",
                  marginBottom: "1%",
                }}
              >
                Voltar
              </Button>
            </div>
            <Row>
              <Col md={4}>
                <img src={val.fotoPerfil} alt="" className="user-img" />
              </Col>
              <Col>
                <h1
                  style={{
                    fontWeight: "bold",
                    color: "green",
                    marginTop: "10%",
                  }}
                >
                  {val.nome}
                </h1>
                <h5>Idade: {getIdade(val.dataNascimento)}</h5>
                <h5>Curso: {val.curso}</h5>
                <h5>Ano de Ingresso: {val.anoIngresso}</h5>
                <h5>Contato: {val.telContato}</h5>
                <br />
                {val.descricao}
              </Col>
            </Row>
          </div>
        );
      })}
    </div>
  );
}

export default UserExpanded;
