import React from "react";
import "./HomeComponent2.css";
import Button from "react-bootstrap/Button";
import Card from "react-bootstrap/Card";
import {
  BsFillHouseDoorFill,
  BsFillPersonFill,
  BsFillPeopleFill,
} from "react-icons/bs";

function HomeComponent2() {
  return (
    <div className="homeComponent2">
      <section className="component2Title">
        <p>Por que usar o IFHome</p>
      </section>
      <section className="component2Subtitle">
        <p>
          Encontre moradia ideal para você! Facilitamos o trabalho de busca para
        </p>
        <p> te ajudar a encontrar o lugar perfeito para morar.</p>
      </section>
      <section className="component2Cards">
        <Card
          style={{ width: "18rem", height: "20rem", marginBottom: "5%" }}
          className="text-center"
        >
          <BsFillHouseDoorFill
            style={{ fontSize: "64px", alignSelf: "center", paddingTop: "5%" }}
          />
          <Card.Body>
            <Card.Title>Encontre uma moradia</Card.Title>
            <Card.Text>
              Encontre o quarto perfeito para você, contate o dono e marque uma
              visita hoje mesmo!
            </Card.Text>
            <Button
              variant="success"
              href="/buscarMoradia"
              className="card-button"
            >
              Encontre um Lugar
            </Button>
          </Card.Body>
        </Card>
        <Card
          style={{ width: "18rem", height: "20rem", marginBottom: "5%" }}
          className="text-center"
        >
          <BsFillPersonFill
            style={{ fontSize: "64px", alignSelf: "center", paddingTop: "5%" }}
          />
          <Card.Body>
            <Card.Title>Encontre Inquilinos</Card.Title>
            <Card.Text>
              Encontre pessoas buscando quartos para alugar e encontre o
              companheiro de casa ou inquilino ideal.
            </Card.Text>
            <Button
              variant="success"
              href="/anunciarVaga"
              className="card-button"
            >
              Anuncie seu Quarto
            </Button>
          </Card.Body>
        </Card>
        <Card
          style={{ width: "18rem", height: "20rem", marginBottom: "5%" }}
          className="text-center"
        >
          <BsFillPeopleFill
            style={{ fontSize: "64px", alignSelf: "center", paddingTop: "5%" }}
          />
          <Card.Body>
            <Card.Title>Monte sua República</Card.Title>
            <Card.Text>
              Se não encontrou o local perfeito? Encontre outras pessoas que
              também estão buscando moradia para montar sua própria república.
            </Card.Text>
            <Button
              variant="success"
              href="/montarRepublica"
              className="card-button"
            >
              Entenda como Funciona
            </Button>
          </Card.Body>
        </Card>
      </section>
    </div>
  );
}

export default HomeComponent2;
