import React, { useState, useEffect, useContext } from "react";
import { AuthContext } from "../../../contexts/auth";
import Axios from "axios";

import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Carousel from "react-bootstrap/Carousel";
import Button from "react-bootstrap/Button";

import { BsHouseFill } from "react-icons/bs";
import { FaMapMarkerAlt } from "react-icons/fa";
import { BsWifi } from "react-icons/bs";
import { FaBed } from "react-icons/fa";
import { FaBath } from "react-icons/fa";
import { FaCar } from "react-icons/fa";
import { BsFillPersonFill } from "react-icons/bs";
import { BiBed } from "react-icons/bi";
import { FaToiletPaper } from "react-icons/fa";

import "./moradiaExpanded.css";

function moradiaExpanded() {
  const { authenticated } = useContext(AuthContext);
  const [vacanciesList, setVacanciesList] = useState([]);
  const [usersList, setUsersList] = useState([]);

  let vagaId = parseInt(window.location.href.toString().slice(30));
  let userId;

  useEffect(() => {
    Axios.post("http://localhost:3001/api/getVaga", { vagaId }).then(
      (response) => {
        setVacanciesList(response.data);
        if (response.data.length < 1) {
          window.location.href = "http://localhost:3000/404";
        }

        if (response.data[0]) userId = response.data[0].Usuarios_idUsuarios;

        Axios.post("http://localhost:3001/api/getUser", { userId }).then(
          (response) => {
            setUsersList(response.data);
            if (response.data.length < 1) {
              window.location.href = "http://localhost:3000/404";
            }
          }
        );
      }
    );
  }, []);

  return (
    <div style={{ paddingBottom: "3%", backgroundColor: "#004d00" }}>
      {vacanciesList.map((val) => {
        return (
          <div
            className="moradiaContainer"
            style={{
              width: "90%",
              margin: "4% auto",
              backgroundColor: "white",
              borderRadius: "20px",
            }}
          >
            <Button
              href="/buscarMoradia"
              variant="success"
              className="moradia-btn"
            >
              Voltar
            </Button>
            <h1
              style={{
                textAlign: "center",
                color: "green",
                fontWeight: "bold",
              }}
            >
              {val.nomeVaga}
            </h1>
            <Carousel
              className="w-75"
              style={{
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                marginLeft: "12.5%",
                marginTop: "2%",
              }}
            >
              <Carousel.Item className="moradiaCarousel">
                <img
                  src={val.imgPrincipal}
                  alt=""
                  className="moradiaCarouselImg"
                />
              </Carousel.Item>
              {/* <Carousel.Item className="moradiaCarousel">
                <img
                  src="https://images.adsttc.com/media/images/5d80/c8bb/284d/d15e/5d00/0915/large_jpg/featured-image-moradia-estudantil-lucien-cornil-a-plus-architecture.jpg?1568721073"
                  alt=""
                  className="moradiaCarouselImg"
                />
              </Carousel.Item> */}
            </Carousel>
            <p
              style={{
                textAlign: "center",
                marginTop: "2%",
                fontSize: "21px",
                color: "green",
              }}
            >
              Valor: R${val.valorAluguel}
              <b>{val.contasInclusas ? "" : " + contas"}</b>
            </p>
            <div>
              <h3 style={{ color: "green", fontWeight: "bold" }}>Descrição:</h3>
              {val.descricao}
            </div>
            <div className="mt-5">
              <h3 style={{ color: "green", fontWeight: "bold" }}>
                O que esse lugar oferece:
              </h3>
              <Container className="mt-4 pb-5">
                <Row>
                  <Col xs={6}>
                    <p style={{ fontSize: "18px" }}>
                      <BsHouseFill /> Área do terreno: {val.areaTerreno}m²
                    </p>
                    <p style={{ fontSize: "18px" }}>
                      <FaMapMarkerAlt /> Bairro: {val.bairro}
                    </p>
                    <p style={{ fontSize: "18px" }}>
                      <BsWifi /> Velocidade da Internet: {val.velInternet}
                      Mbps
                    </p>
                    <p style={{ fontSize: "18px" }}>
                      <FaBed /> Nº de Quartos: {val.qntdQuartos}
                    </p>
                    {val.qntdBanheiros > 0 && (
                      <p style={{ fontSize: "18px" }}>
                        <FaBath /> Nº de Banheiros: {val.qntdBanheiros}
                      </p>
                    )}
                  </Col>
                  <Col xs={6}>
                    {val.vagaEstacionamento > 0 && (
                      <p style={{ fontSize: "18px" }}>
                        <FaCar /> Vaga de Estacionamento
                      </p>
                    )}
                    {val.quartoIndividual > 0 && (
                      <p style={{ fontSize: "18px" }}>
                        <BsFillPersonFill /> Quarto Individual
                      </p>
                    )}
                    {val.quartoMobiliado > 0 && (
                      <p style={{ fontSize: "18px" }}>
                        <BiBed /> Quarto Mobiliado
                      </p>
                    )}
                    {val.banheiroIndividual > 0 && (
                      <p style={{ fontSize: "18px" }}>
                        <FaToiletPaper /> Banheiro Individual
                      </p>
                    )}
                  </Col>
                  <Col></Col>
                </Row>
              </Container>
            </div>
            {usersList.map((val) => {
              return (
                <div className="pb-5">
                  <h3 style={{ color: "green", fontWeight: "bold" }}>
                    Informações do Locador:
                  </h3>
                  {authenticated === false && (
                    <div style={{ fontSize: "21px" }}>
                      <p className="mb-0">
                        <b>Nome: </b>
                        ********
                      </p>
                      <p className="mb-0">(**) *****-****</p>
                      <p className="mb-0">
                        <b>
                          {val.aluno
                            ? "É aluno do IFSP"
                            : " Não é aluno do IFSP"}
                        </b>
                      </p>
                    </div>
                  )}
                  {authenticated === true && (
                    <div style={{ fontSize: "21px" }}>
                      <p className="mb-0">
                        <b>Nome: </b>
                        {val.nome}
                      </p>
                      <p className="mb-0">{val.telContato}</p>
                      <p className="mb-0">
                        <b>
                          {val.aluno
                            ? "É aluno do IFSP"
                            : " Não é aluno do IFSP"}
                        </b>
                      </p>
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        );
      })}
    </div>
  );
}

export default moradiaExpanded;
