import React, { createContext, useState, useEffect } from "react";
import Axios from "axios";
import { useNavigate } from "react-router-dom";

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const navigate = useNavigate();
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const recoveredUser = localStorage.getItem("user");
    if (recoveredUser) {
      setUser(JSON.parse(recoveredUser));
    }
    setLoading(false);
  }, []);

  const login = (cpf, password) => {
    Axios.post("http://localhost:3001/api/getUserLogin", {
      cpf,
      password,
    }).then((response) => {
      if (response.data.length < 1) {
        alert("CPF ou senha incorretos");
      } else {
        const loggedUser = {
          id: response.data[0].idUsuarios,
          nome: response.data[0].nome,
          cpf: response.data[0].cpf,
          email: response.data[0].email,
          telContato: response.data[0].telContato,
          curso: response.data[0].curso,
          procuraVaga: response.data[0].procuraVaga,
        };
        localStorage.setItem("user", JSON.stringify(loggedUser));
        setUser({ id: response.data.idUsuarios, nome: response.data.nome });
        navigate("/");
        window.location.reload(false);
      }
    });
  };

  const logout = () => {
    localStorage.removeItem("user");
    setUser(null);
    navigate("/");
    window.location.reload(false);
  };
  return (
    <AuthContext.Provider
      value={{ authenticated: !!user, user, loading, login, logout }}
    >
      {children}
    </AuthContext.Provider>
  );
};
