import React, { useState, useEffect, useContext } from "react";
import Axios from "axios";
import { AuthContext } from "../../../contexts/auth";

import "./myVagas.css";

import { Card } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import { FaMapMarkerAlt } from "react-icons/fa";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Badge from "react-bootstrap/Badge";

function myVagas() {
  const { user } = useContext(AuthContext);
  const [vacanciesList, setVacanciesList] = useState([]);

  console.log(user.id);
  const userId = user.id;

  useEffect(() => {
    Axios.post("http://localhost:3001/api/getUserVagas", { userId }).then(
      (response) => {
        setVacanciesList(response.data);
      }
    );
  }, []);
  return (
    <>
      {vacanciesList < 1 && (
        <h4 className="pt-5 ps-5 text-light">
          Você não possui vagas cadastradas na plataforma
        </h4>
      )}
      <div>
        <h1
          style={{
            paddingTop: "2%",
            textAlign: "center",
            color: "white",
          }}
        >
          Minhas Vagas
        </h1>
      </div>
      <section className="cards">
        {vacanciesList.map((val) => {
          return (
            <Card style={{ width: "16rem", height: "27rem" }} className="card">
              {val.active === 0 && (
                <Badge bg="danger" className="cardBadge">
                  Desativada
                </Badge>
              )}
              <Card.Img
                variant="top"
                src={val.imgPrincipal}
                className="card-img"
              ></Card.Img>
              <Card.Body className="text-center">
                <Card.Title style={{ fontSize: "16px" }}>
                  {val.nomeVaga} &nbsp;
                </Card.Title>

                <Card.Title style={{ fontSize: "14px" }}>
                  <FaMapMarkerAlt /> {val.bairro}
                </Card.Title>
                <Card.Text style={{ fontSize: "14px" }}>
                  R${val.valorAluguel}{" "}
                  <b>{val.contasInclusas ? "" : " + contas"}</b>
                </Card.Text>
                <Card.Text style={{ fontSize: "12px" }}>
                  {val.descricao}
                </Card.Text>
                {val.active === 1 && (
                  <Row className="cardRow">
                    <Col md>
                      <Button
                        href={"/atualizarInfosVaga/" + val.idVaga}
                        variant="success"
                        size="sm"
                        style={{ fontSize: "13px", whiteSpace: "nowrap" }}
                      >
                        Editar Vaga
                      </Button>
                    </Col>
                    <Col md>
                      <Button
                        variant="outline-danger"
                        size="sm"
                        style={{ fontSize: "13px", whiteSpace: "nowrap" }}
                        onClick={() => {
                          Axios.post(
                            "http://localhost:3001/api/disableVacancy",
                            {
                              idVaga: val.idVaga,
                            }
                          ).then((response) => {
                            window.location.assign("/minhasVagas");
                          });
                        }}
                      >
                        <label>Desativar Vaga</label>
                      </Button>
                    </Col>
                  </Row>
                )}
                {val.active === 0 && (
                  <Row className="cardRow">
                    <Col md>
                      <Button
                        href={"/atualizarInfosVaga/" + val.idVaga}
                        variant="success"
                        size="sm"
                        style={{ fontSize: "14px", whiteSpace: "nowrap" }}
                      >
                        Editar Vaga
                      </Button>
                    </Col>
                    <Col md>
                      <Button
                        variant="outline-success"
                        size="sm"
                        style={{ fontSize: "14px", whiteSpace: "nowrap" }}
                        onClick={() => {
                          Axios.post(
                            "http://localhost:3001/api/activateVacancy",
                            {
                              idVaga: val.idVaga,
                            }
                          ).then((response) => {
                            window.location.assign("/minhasVagas");
                          });
                        }}
                      >
                        <label>Ativar Vaga</label>
                      </Button>
                    </Col>
                  </Row>
                )}
              </Card.Body>
            </Card>
          );
        })}
      </section>
    </>
  );
}

export default myVagas;
