import React, { useContext } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";

import { AuthProvider, AuthContext } from "./contexts/auth";

import Home from "./pages/Home";
import BuscarMoradia from "./pages/BuscarMoradia";
import Moradia from "./pages/Moradia";
import Register from "./pages/Register";
import AnunciaMoradia from "./pages/AnunciaMoradia";
import Login from "./pages/Login";
import MontarRepublica from "./pages/MontarRepublica";
import AtualizarInfosUser from "./pages/AtualizarInfosUser";
import MinhasVagas from "./pages/MinhasVagas";
import About from "./pages/About";
import Usuario from "./pages/Usuario";
import AtualizarInfosVaga from "./pages/AtualizarInfosVaga";
import Page404 from "./pages/Page404";

const AppRoutes = () => {
  const Private = ({ children }) => {
    const { authenticated, loading } = useContext(AuthContext);

    if (loading) {
      return <div className="loading">Carregando...</div>;
    }

    if (!authenticated) {
      return <Navigate to="/login" />;
    }
    return children;
  };

  return (
    <Router>
      <AuthProvider>
        <Routes>
          <Route path="/" element={<Home />}></Route>
          <Route path="/home" element={<Home />}></Route>
          <Route path="/sobre" element={<About />}></Route>
          <Route path="/buscarMoradia" element={<BuscarMoradia />}></Route>
          <Route path={"/moradia/" + "*"} element={<Moradia />}></Route>
          <Route
            path={"/user/" + "*"}
            element={
              <Private>
                <Usuario />
              </Private>
            }
          ></Route>
          <Route path="/registro" element={<Register />}></Route>
          <Route
            path="/anunciarVaga"
            element={
              <Private>
                <AnunciaMoradia />
              </Private>
            }
          ></Route>
          <Route path="/login" element={<Login />}></Route>
          <Route path="/montarRepublica" element={<MontarRepublica />}></Route>
          <Route
            path="/atualizarInfosUser"
            element={
              <Private>
                <AtualizarInfosUser />
              </Private>
            }
          ></Route>
          <Route
            path="/minhasVagas"
            element={
              <Private>
                <MinhasVagas />
              </Private>
            }
          ></Route>
          <Route
            path={"/atualizarInfosVaga/" + "*"}
            element={
              <Private>
                <AtualizarInfosVaga />
              </Private>
            }
          ></Route>
          <Route path={"/404"} element={<Page404 />}></Route>
        </Routes>
      </AuthProvider>
    </Router>
  );
};

export default AppRoutes;
