import React, { useContext } from "react";
import { AuthContext } from "../../../contexts/auth";

import logoIFHome from "../../../../src/img/icon12.png";

import "./Header.css";

import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import Button from "react-bootstrap/Button";

import { BiExit } from "react-icons/bi";

function Header() {
  const { authenticated, logout } = useContext(AuthContext);

  const handleLogout = () => {
    logout();
  };

  return (
    <Navbar collapseOnSelect expand="lg" bg="white" variant="white">
      <Container>
        <Navbar.Brand href="/home">
          <img src={logoIFHome} alt="IFHOME" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link href="/sobre" className="navlink">
              Sobre
            </Nav.Link>
            {authenticated === true && (
              <Nav.Link href="/minhasVagas" className="navlink">
                Minhas Vagas
              </Nav.Link>
            )}
            <Nav.Link href="/buscarMoradia" className="navlink">
              Encontrar moradia
            </Nav.Link>
            <Nav.Link href="/montarRepublica" className="navlink">
              Montar República
            </Nav.Link>
            <Nav.Link href="/anunciarVaga" className="navlink">
              Anunciar Vaga
            </Nav.Link>
          </Nav>
          {authenticated === false && (
            <Nav>
              <Button
                href="/registro"
                variant="outline-success"
                style={{
                  marginRight: "5%",
                  marginLeft: "5%",
                  marginBottom: "2%",
                }}
              >
                Cadastrar
              </Button>
              <Button
                href="/login"
                variant="success"
                style={{
                  marginRight: "5%",
                  marginLeft: "5%",
                  marginBottom: "2%",
                  whiteSpace: "nowrap",
                }}
              >
                Entrar &nbsp;
                <BiExit />
              </Button>
            </Nav>
          )}
          {authenticated === true && (
            <Nav>
              <Button
                variant="outline-success"
                style={{
                  whiteSpace: "nowrap",
                }}
                href="/atualizarInfosUser"
              >
                Meu Cadastro
              </Button>
              <Button
                variant="success"
                style={{
                  marginRight: "5%",
                  marginLeft: "5%",
                  marginBottom: "2%",
                  whiteSpace: "nowrap",
                }}
                onClick={handleLogout}
              >
                Sair &nbsp;
                <BiExit />
              </Button>
            </Nav>
          )}
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Header;
