import React, { useState, useEffect, useContext } from "react";
import { AuthContext } from "../../../contexts/auth";
import Axios from "axios";

import foto from "../../../img/2.png";

import "./StudentsCards.css";

import { Card } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import FilterStudents from "../../filterStudents/FilterStudents";

function StudentsCards() {
  const { authenticated } = useContext(AuthContext);
  const [studentsList, setStudentsList] = useState([]);
  const [filterValue, setFilterValue] = useState();

  let filteredStudentsList = studentsList.filter((student) => {
    if (
      filterValue === "" ||
      filterValue === null ||
      filterValue === undefined ||
      filterValue === "Todos"
    ) {
      return student;
    } else if (filterValue === "Engenharia da Computação") {
      return student.curso === "Engenharia da Computação";
    } else if (filterValue === "Engenharia Mecanica") {
      return student.curso === "Engenharia Mecanica";
    } else if (filterValue === "Engenharia Elétrica") {
      return student.curso === "Engenharia Elétrica";
    } else if (filterValue === "Física") {
      return student.curso === "Física";
    } else if (filterValue === "Automação Industrial") {
      return student.curso === "Automação Industrial";
    }
    return student.curso === "";
  });

  useEffect(() => {
    Axios.get("http://localhost:3001/api/getUsers").then((response) => {
      setStudentsList(response.data);
    });
  }, []);

  function onFilterValueSelected(filterValue) {
    setFilterValue(filterValue);
  }

  return (
    <>
      <div>
        <h1
          style={{
            paddingTop: "2%",
            paddingBottom: "1%",
            textAlign: "center",
            color: "white",
          }}
        >
          Montar República
        </h1>
      </div>
      <FilterStudents
        filterValueSelected={onFilterValueSelected}
      ></FilterStudents>
      <section className="student-cards">
        {filteredStudentsList.map((val) => {
          return (
            <Card style={{ width: "12rem", height: "21rem" }} className="mb-3">
              {authenticated === true && (
                <Card.Img
                  variant="top"
                  src={val.fotoPerfil}
                  className="student-card-img-auth"
                ></Card.Img>
              )}
              {authenticated === false && (
                <Card.Img
                  variant="top"
                  src={foto}
                  className="student-card-img"
                ></Card.Img>
              )}
              <Card.Body className="text-center">
                {authenticated === true && (
                  <Card.Title style={{ fontSize: "14px" }}>
                    {val.nome}
                  </Card.Title>
                )}
                {authenticated === false && (
                  <Card.Title style={{ fontSize: "14px" }}>
                    Faça Login para ver as informações
                  </Card.Title>
                )}
                <Card.Title style={{ fontSize: "12px" }}>
                  {val.curso}
                </Card.Title>
                <Card.Text style={{ fontSize: "12px" }}>
                  Ano de Ingresso: {val.anoIngresso}
                </Card.Text>
                {authenticated === true && (
                  <Button
                    variant="success"
                    size="sm"
                    href={"/user/" + val.idUsuarios}
                    className="student-card-btn"
                  >
                    Mais Informações
                    {/* <BsFillInfoCircleFill /> */}
                  </Button>
                )}
                {authenticated === false && (
                  <Button
                    variant="success"
                    size="sm"
                    className="student-card-btn"
                    disabled
                  >
                    Mais Informações
                    {/* <BsFillInfoCircleFill /> */}
                  </Button>
                )}
              </Card.Body>
            </Card>
          );
        })}
      </section>
    </>
  );
}

export default StudentsCards;
