import React, { useState, useContext } from "react";
import { AuthContext } from "../../contexts/auth";
import Axios from "axios";

import InputMask from "react-input-mask";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import Form from "react-bootstrap/Form";

import "./AnnounceVacancy.css";

function RegisterComponent() {
  const { user } = useContext(AuthContext);
  let userId;

  function cadastrarVaga() {
    userId = user.id;
    console.log(fotoPerfil);
    if (
      nomeVaga === "" ||
      descricao === "" ||
      tipoVaga === "" ||
      valorAluguel === "" ||
      areaTerreno === "" ||
      qtdQuartos === "" ||
      qtdBanheiros === "" ||
      velInternet === "" ||
      fotoPerfil === null
    ) {
      alert("Informações faltando. Verifique e tente novamente");
    } else if (
      !bairro ||
      bairro === "" ||
      bairro === null ||
      bairro === undefined
    ) {
      alert("CEP Inválido");
    } else if (cidade !== "Piracicaba") {
      alert("Cidade inválida. Só são aceitos CEPs da cidade de Piracicaba");
    } else if (valorAluguel < 1) {
      alert("Digite um valor de aluguel válido");
    } else if (areaTerreno < 1) {
      alert("Digite uma área de terreno válida");
    } else if (qtdQuartos < 1 || qtdBanheiros < 1) {
      alert("Digite o número de quartos e banheiros corretamente");
    } else if (velInternet < 1) {
      alert("Digite uma velocidade de internet válida");
    } else if (fotoFile.type.startsWith("image/")) {
      insertUsuario();
    } else {
      alert("Tipo de arquivo de foto inválido");
    }
  }

  const insertUsuario = () => {
    Axios.post("http://localhost:3001/api/insertVaga", {
      nomeVaga: nomeVaga,
      descricao: descricao,
      tipoVaga: tipoVaga,
      valorAluguel: valorAluguel,
      cep: cep,
      areaTerreno: areaTerreno,
      qtdQuartos: qtdQuartos,
      qtdBanheiros: qtdBanheiros,
      quartoIndividual: quartoIndividual,
      quartoMobiliado: quartoMobiliado,
      contasInclusas: contasInclusas,
      vagaEstacionamento: vagaEstacionamento,
      banheiroIndividual: banheiroIndividual,
      velInternet: velInternet,
      bairro: bairro,
      userId,
      fotoPerfil: fotoPerfil,
    })
      .then((response) => {
        // console.log(response);
        if (response.data !== "") {
          alert("ERRO: Algo deu errado");
        } else {
          alert("Vaga cadastrada com sucesso!");
          window.location.assign("/buscarMoradia");
        }
      })
      .catch((err) => {
        if (err.response) {
          alert("ERRO: Foto muito pesada. Escolha uma de até 2MB");
          console.log(err.response.data);
          console.log(err.response.status);
          console.log(err.response.headers);
        }
      });
  };

  const checkCEP = (e) => {
    if (!e.target.value) return;
    const cep = e.target.value.replace(/\D/g, "");
    setCep(cep);
    const url = `http://viacep.com.br/ws/${cep}/json/`;
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        // console.log(data);
        setBairro(data.bairro);
        setCidade(data.localidade);
      });
  };

  const [nomeVaga, setNomeVaga] = useState("");
  const [descricao, setDescricao] = useState("");
  const [tipoVaga, setTipoVaga] = useState(
    "Apartamento - Quarto Individual - Feminino"
  );
  const [valorAluguel, setValorAluguel] = useState("");
  const [cep, setCep] = useState("");
  const [areaTerreno, setAreaTerreno] = useState("");
  const [qtdQuartos, setQtdQuartos] = useState("");
  const [qtdBanheiros, setQtdBanheiros] = useState("");
  const [quartoIndividual, setQuartoIndividual] = useState(false);
  const [quartoMobiliado, setQuartoMobiliado] = useState(false);
  const [contasInclusas, setContasInclusas] = useState(false);
  const [vagaEstacionamento, setVagaEstacionamento] = useState(false);
  const [banheiroIndividual, setBanheiroIndividual] = useState(false);
  const [velInternet, setVelInternet] = useState("");
  const [fotoPerfil, setFotoPerfil] = useState(null);
  const [bairro, setBairro] = useState("");
  const [cidade, setCidade] = useState("");
  const [fotoFile, setFotoFile] = useState(null);

  return (
    <>
      <div style={{ paddingBottom: "5%", backgroundColor: "#004d00" }}>
        <h1
          style={{
            paddingTop: "2%",
            textAlign: "center",
            color: "white",
          }}
        >
          Anuncie sua Vaga
        </h1>
        <div
          style={{
            marginTop: "5%",
            width: "60%",
            margin: "3% auto",
            backgroundColor: "white",
            borderRadius: "20px",
            paddingBottom: "3%",
          }}
        >
          <div className="form" id="form">
            <div className="pt-5">
              <label>Nome da Vaga</label> &nbsp;
              <small>maximo de 30 caracteres</small> <br />
              <input
                placeholder="Quarto Compartilhado, Suíte Invidual, República mista, etc"
                className="input"
                maxLength="30"
                onChange={(e) => {
                  setNomeVaga(e.target.value);
                }}
              />
            </div>
            <div className="pt-4">
              <label>Tipo da Vaga</label> <br />
              <select
                onChange={(e) => {
                  setTipoVaga(e.target.value);
                }}
              >
                <option value="Apartamento - Quarto Individual - Feminino">
                  Apartamento - Quarto Individual - Feminino
                </option>
                <option value="Apartamento - Quarto Individual - Masculino">
                  Apartamento - Quarto Individual - Masculino
                </option>
                <option value="Apartamento - Quarto Individual - Misto">
                  Apartamento - Quarto Individual - Misto
                </option>
                <option value="Apartamento - Quarto Coletivo - Feminino">
                  Apartamento - Quarto Coletivo - Feminino
                </option>
                <option value="Apartamento - Quarto Coletivo - Masculino">
                  Apartamento - Quarto Coletivo - Masculino
                </option>
                <option value="Apartamento - Quarto Coletivo - Misto">
                  Apartamento - Quarto Coletivo - Misto
                </option>
                <option value="Casa - Quarto Individual - Feminino">
                  Casa - Quarto Individual - Feminino
                </option>
                <option value="Casa - Quarto Individual - Masculino">
                  Casa - Quarto Individual - Masculino
                </option>
                <option value="Casa - Quarto Individual - Misto">
                  Casa - Quarto Individual - Misto
                </option>
                <option value="Casa - Quarto Coletivo - Feminino">
                  Casa - Quarto Coletivo - Feminino
                </option>
                <option value="Casa - Quarto Coletivo - Masculino">
                  Casa - Quarto Coletivo - Masculino
                </option>
                <option value="Casa - Quarto Coletivo - Misto">
                  Casa - Quarto Coletivo - Misto
                </option>
                <option value="Kitnet">Kitnet</option>
                <option value="Outros">Outros</option>
              </select>
            </div>
            <div className="pt-4">
              <label>Descrição da Vaga</label> &nbsp;
              <small>máximo de 150 caracteres</small> <br />
              <input
                placeholder="Escreva aqui informações mais detalhadas da vaga e das pessoas que moram lá (caso haja)"
                maxLength="150"
                className="input-description"
                onChange={(e) => {
                  setDescricao(e.target.value);
                }}
              />
            </div>
            <Row className="pt-4">
              <label>
                Valor Mensal do Aluguel <small>em R$</small>
              </label>
              <br />
              <Col md>
                <div>
                  <input
                    placeholder="500.00"
                    type="number"
                    min={1}
                    className="input"
                    onChange={(e) => {
                      setValorAluguel(e.target.value);
                    }}
                  />
                </div>
              </Col>
              <Col md>
                <div>
                  <input
                    value={contasInclusas}
                    type="checkbox"
                    onChange={(e) => setContasInclusas(e.target.checked)}
                  />
                  &nbsp;
                  <label>As contas já vêm inclusas nesse valor?</label> <br />
                </div>
              </Col>
            </Row>
            <Row className="pt-4">
              <br />
              <Col md>
                <div>
                  <label>
                    Área do Terreno <small>em m²</small>
                  </label>
                  <input
                    placeholder="100"
                    type="number"
                    min={1}
                    className="input"
                    onChange={(e) => {
                      setAreaTerreno(e.target.value);
                    }}
                  />
                </div>
              </Col>
              <Col md>
                <div>
                  <label>CEP</label> <br />
                  <InputMask
                    placeholder="12345-678"
                    mask="99999-999"
                    className="input"
                    onBlur={checkCEP}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md>
                <div className="pt-4">
                  <label>Quantidade total de quartos na moradia</label> <br />
                  <input
                    placeholder="3"
                    type="number"
                    className="input"
                    min={1}
                    onChange={(e) => {
                      setQtdQuartos(e.target.value);
                    }}
                  />
                </div>
              </Col>
              <Col md>
                <div className="pt-4">
                  <label>Quantidade total de banheiros na moradia</label> <br />
                  <input
                    placeholder="2"
                    type="number"
                    min={1}
                    className="input"
                    onChange={(e) => {
                      setQtdBanheiros(e.target.value);
                    }}
                  />
                </div>
              </Col>
            </Row>
            <Row>
              <Col md>
                <div className="pt-4">
                  <input
                    value={quartoIndividual}
                    type="checkbox"
                    onChange={(e) => setQuartoIndividual(e.target.checked)}
                  />
                  &nbsp;
                  <label>Quarto é individual?</label> <br />
                </div>
                <div>
                  <input
                    value={banheiroIndividual}
                    type="checkbox"
                    onChange={(e) => setBanheiroIndividual(e.target.checked)}
                  />
                  &nbsp;
                  <label>Banheiro é individual?</label> <br />
                </div>
              </Col>
              <Col md>
                <div className="pt-4">
                  <input
                    value={quartoMobiliado}
                    type="checkbox"
                    onChange={(e) => setQuartoMobiliado(e.target.checked)}
                  />
                  &nbsp;
                  <label>Quarto é mobiliado?</label> <br />
                </div>
                <div>
                  <input
                    value={vagaEstacionamento}
                    type="checkbox"
                    onChange={(e) => setVagaEstacionamento(e.target.checked)}
                  />
                  &nbsp;
                  <label>Possui vaga de estacionamento?</label> <br />
                </div>
              </Col>
            </Row>
            <Row className="pt-4">
              <label>
                Velocidade do Wi-Fi <small>em Mbps</small>
              </label>{" "}
              <br />
              <Col md>
                <div>
                  <input
                    placeholder="100"
                    type="number"
                    min={1}
                    className="input"
                    onChange={(e) => {
                      setVelInternet(e.target.value);
                    }}
                  />
                </div>
              </Col>
              <Col md></Col>
            </Row>

            <div className="pt-4">
              <Form.Group className="mb-3">
                <Form.Label style={{ color: "black" }}>
                  Foto principal para a vaga
                </Form.Label>
                <Form.Control
                  type="file"
                  id="foto"
                  name="foto"
                  max-size="50"
                  accept="image/png, image/jpeg"
                  onChange={(e) => {
                    const file = e.target.files[0];
                    const reader = new FileReader();
                    reader.addEventListener("load", () => {
                      setFotoPerfil(reader.result);
                    });
                    if (file !== undefined) {
                      reader.readAsDataURL(file);
                      setFotoFile(file);
                    }
                  }}
                />
              </Form.Group>
            </div>
            <Button
              variant="success"
              className="mt-4"
              onClick={cadastrarVaga}
              size="lg"
            >
              Anunciar Vaga
            </Button>
          </div>
        </div>
      </div>
    </>
  );
}

export default RegisterComponent;
